body {
  margin: 0;
}

$cell-side: 100px;
$cell-background-color: #eee;
$cell-border: 1px solid #000;

$calendar-gap: 1px;
$calendar-padding: 10px;
$calendar-width: $cell-side * 7 + 6 * $calendar-gap + $calendar-padding * 2;

$days: "mon",
  "tue"$cell-side + $calendar-gap,
  "wed"($cell-side + $calendar-gap) * 2,
  "thu"($cell-side + $calendar-gap) * 3,
  "fri"($cell-side + $calendar-gap) * 4,
  "sat"($cell-side + $calendar-gap) * 5,
  "sun"($cell-side + $calendar-gap) * 6;

.content {
  display: flex;
  justify-content: center;
}

.calendar {
  display: flex;
  flex-wrap: wrap;
  gap: $calendar-gap;
  width: $calendar-width;
  padding: $calendar-padding;

  &__cell {
    display: flex;
    justify-content: center;
    align-items: center;

    box-sizing: border-box;
    width: $cell-side;
    height: $cell-side;

    background-color: $cell-background-color;
    border: $cell-border;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 30px;

    cursor: pointer;
    transition: transform 500ms;

    @for $index from 1 through 31 {
      &:nth-child(#{$index})::before {
        content: "#{$index}";
      }
    }

    &:hover {
      background-color: pink;
      transform: translateY(-20px);
    }
  }

  @for $index from 28 through 31 {
    &--month-length--#{$index} :nth-child(n + #{$index + 1}) {
      display: none;
    }
  }

  @each $day,
    $margin-left in $days {
    &--start-day--#{$day} :first-child {
      margin-left: $margin-left;
    }
  }
}
